import tabControl from '@/components/tabControl-tag/tabControl-tag.vue';
export default {
	components: {
		tabControl
	},
	data() {
		return {
			current: 0,
			items: ['全部', '最新', '好评','差评'],
			shopid: 0,
			replayList: [],
			itemList: [],
			itemList2: [],
			itemList3: [],
			itemList4: [],
		}
	},
	onLoad:function(option){
		this.shopid = option.shopid
	},
	onShow:function(){
		var that = this
		// 获取所有评论
		uni.request({
			url: this.$bashUrl+ '/index.php/home/Api/comment_store/',
			method: 'POST',
			data:{
				id: this.shopid
			},
			success:(res=>{
				that.itemList = []
				that.itemList2 = []
				that.itemList3 = []
				that.itemList4 = []
				console.log(res.data)
				that.itemList = res.data
				that.itemList2 = res.data.sort((a,b)=> new Date(b.intime).getTime() - new Date(a.intime).getTime() )
				// that.itemList3 = 
				res.data.forEach(item=>{
					if(item.score >= 4){
						that.itemList3.push(item)
					}else{
						that.itemList4.push(item)
					}
				})
			})
		})
	},
	methods: {
		onClickItem:function(val) {
			this.current = val.currentIndex
			console.log("点击了事件"+val.currentIndex)
		},
		replayFunction:function(id){
			var that = this
			uni.navigateTo({
				url:'/pages/BusinessSide/addReplay/addReplay?id=' + id +"&shopid=" + that.shopid
			})
		},
		// 刷新方法
		refresh:function(){
			uni.request({
				url: this.$bashUrl+ '/index.php/home/Api/comment_store/',
				method: 'POST',
				data:{
					id: this.shopid
				},
				success:(res=>{
					that.itemList = res.data
				})
			})
		}
	}
}
